import React, { useEffect, useState } from "react";
import "../Styles/Eform.css";
import { useNavigate } from "react-router-dom";

import { Box, Button, Grid, Paper, TextField, Typography } from "@mui/material";
import axios from "axios";
import { Formik, useFormik } from "formik";
import { EFormSchema } from "../Schema/EFormSchema";
// const initialValues = {
//   name: "",
//   email: "",
//   phone: "",
//   address: "",
//   designation: "",
//   location: "",
//   salary: "",
//   date_of_joining: "",
// };

const Eform = (props) => {
  const [userToken, setUserToken] = useState(
    sessionStorage.getItem("userToken")
  );
  const [userId, setUserId] = useState(sessionStorage.getItem("userId"));

  const [userRole, setUserRole] = useState(sessionStorage.getItem("role"));
  const {
    values,
    errors,
    handleBlur,
    touched,
    handleChange,
    handleSubmit,
    resetForm,
  } = useFormik({
    initialValues: props.data,
    validationSchema: EFormSchema,
    onSubmit: (values) => {
      console.log(values);
      let data = {
        userId: userId,
        token: userToken,
        role: userRole,
        name: values.name,
        email: values.email,
        phone: values.phone,
        address: values.address,
        designation: values.designation,
        location: values.location,
        salary: values.salary,
        date_of_joining: values.date_of_joining,
      };
      // console.log(employee);
      if (props.method === "post") {
        axios
          .post("/api/employeelist", data)
          .then((response) => {
            if (response.data.message === "Employee added Successful") {
              alert(response.data.message);
              navigate("/list");
            } else if (response.data.message === "Unauthorised user") {
              alert(response.data.message);
              navigate("/");
            }
          })
          .catch((err) => {
            console.log(err);
            alert("unable to load");
            navigate("/addemployee");
          });
      }
      if (props.method === "put") {
        axios
          .put(`/api/employeelist/${values._id}`, data)
          .then((response) => {
            if (response.data.message === "Updated successfully") {
              alert(response.data.message);
              window.location.reload();
            } else if (response.data.message === "Unauthorised user") {
              alert(response.data.message);
              navigate("/");
            }
          })
          .catch((err) => {
            console.log(err);
            alert("unable to load");
            navigate("/addemployee");
          });
      }
    },
  });
  const [employee, setEmployee] = useState(props.data);
  const navigate = useNavigate();

  useEffect(() => {
    if (!userToken) {
      alert("Unauthorized user");
      navigate("/");
    }
  }, []);

  const inputHandler = (e) => {
    const { name, value } = e.target;
    setEmployee({ ...employee, [name]: value });
  };

  const showList = () => {
    window.location.reload();
  };
  const clearInput = () => {
    resetForm();
  };

  // const addEmployee = (e) => {
  //   e.preventDefault();
  //   let data = {
  //     userId: userId,
  //     token: userToken,
  //     role: userRole,
  //     name: employee.name,
  //     email: employee.email,
  //     phone: employee.phone,
  //     address: employee.address,
  //     designation: employee.designation,
  //     location: employee.location,
  //     salary: employee.salary,
  //     date_of_joining: employee.date_of_joining,
  //   };
  //   console.log(employee);
  //   if (props.method === "post") {
  //     axios
  //       .post("http://localhost:8000/api/employeelist", data)
  //       .then((response) => {
  //         if (response.data.message === "Employee added Successful") {
  //           alert(response.data.message);
  //           navigate("/list");
  //         } else if (response.data.message === "Unauthorised user") {
  //           alert(response.data.message);
  //           navigate("/");
  //         }
  //       })
  //       .catch((err) => {
  //         console.log(err);
  //         alert("unable to load");
  //         navigate("/addemployee");
  //       });
  //   }
  //   if (props.method === "put") {
  //     axios
  //       .put(`http://localhost:8000/api/employeelist/${employee._id}`, data)
  //       .then((response) => {
  //         if (response.data.message === "Updated successfully") {
  //           alert(response.data.message);
  //           window.location.reload();
  //         } else if (response.data.message === "Unauthorised user") {
  //           alert(response.data.message);
  //           navigate("/");
  //         }
  //       })
  //       .catch((err) => {
  //         console.log(err);
  //         alert("unable to load");
  //         navigate("/addemployee");
  //       });
  //   }
  // };

  return (
    <Grid>
      <Paper elevation={0} className="paperStyle">
        <Grid align="center">
          <Grid>
            <Typography varient="caption" sx={{ color: "#FC4A9D" }}>
              Please fill this form to add new employee
            </Typography>
          </Grid>
        </Grid>
        <form onSubmit={handleSubmit}>
          <Grid container spacing={2}>
            <Grid item xs={6}>
              <TextField
                fullWidth
                sx={{ m: 2 }}
                label="Name"
                value={values.name}
                name="name"
                variant="outlined"
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <Box pl={3}>
                {errors.name && touched.name ? (
                  <Typography variant="body2" color="error" gutterBottom>
                    {errors.name}
                  </Typography>
                ) : null}
              </Box>
            </Grid>

            <Grid item xs={6}>
              <TextField
                fullWidth
                sx={{ m: 2 }}
                name="email"
                value={values.email}
                type="email"
                label="Email"
                variant="outlined"
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <Box pl={3}>
                {errors.email && touched.email ? (
                  <Typography variant="body2" color="error" gutterBottom>
                    {errors.email}
                  </Typography>
                ) : null}
              </Box>
            </Grid>

            <Grid item xs={6}>
              <TextField
                fullWidth
                sx={{ m: 2 }}
                name="phone"
                value={values.phone}
                type="tel"
                label="Phone"
                variant="outlined"
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <Box pl={3}>
                {errors.phone && touched.phone ? (
                  <Typography variant="body2" color="error" gutterBottom>
                    {errors.phone}
                  </Typography>
                ) : null}
              </Box>
            </Grid>
            <Grid item xs={6}>
              <TextField
                fullWidth
                sx={{ m: 2 }}
                id="outlined-multiline-static"
                name="address"
                value={values.address}
                multiline
                label="Address"
                variant="outlined"
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <Box pl={3}>
                {errors.address && touched.address ? (
                  <Typography variant="body2" color="error" gutterBottom>
                    {errors.address}
                  </Typography>
                ) : null}
              </Box>
            </Grid>
            <Grid item xs={6}>
              <TextField
                fullWidth
                sx={{ m: 2 }}
                name="designation"
                value={values.designation}
                label="Designation"
                variant="outlined"
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <Box pl={3}>
                {errors.designation && touched.designation ? (
                  <Typography variant="body2" color="error" gutterBottom>
                    {errors.designation}
                  </Typography>
                ) : null}
              </Box>
            </Grid>

            <Grid item xs={6}>
              <TextField
                fullWidth
                sx={{ m: 2 }}
                name="location"
                value={values.location}
                label="Location"
                variant="outlined"
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <Box pl={3}>
                {errors.location && touched.location ? (
                  <Typography variant="body2" color="error" gutterBottom>
                    {errors.location}
                  </Typography>
                ) : null}
              </Box>
            </Grid>
            <Grid item xs={6}>
              <TextField
                fullWidth
                sx={{ m: 2 }}
                name="salary"
                value={values.salary}
                label="Salary"
                variant="outlined"
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <Box pl={3}>
                {errors.salary && touched.salary ? (
                  <Typography variant="body2" color="error" gutterBottom>
                    {errors.salary}
                  </Typography>
                ) : null}
              </Box>
            </Grid>
            <Grid item xs={6}>
              <TextField
                fullWidth
                sx={{ m: 2 }}
                name="date_of_joining"
                value={values.date_of_joining}
                label="Date of joining"
                type="date"
                variant="outlined"
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <Box pl={3}>
                {errors.date_of_joining && touched.date_of_joining ? (
                  <Typography variant="body2" color="error" gutterBottom>
                    {errors.date_of_joining}
                  </Typography>
                ) : null}
              </Box>
            </Grid>
            <Grid item xs={6}>
              {props.method === "post" ? (
                <Button
                  sx={{ padding: "4%", marginLeft: "6%" }}
                  type="reset"
                  fullWidth
                  variant="outlined"
                  color="secondary"
                  onClick={clearInput}
                >
                  Reset
                </Button>
              ) : (
                <Button
                  sx={{ padding: "4%", marginLeft: "6%" }}
                  type="reset"
                  fullWidth
                  variant="outlined"
                  color="secondary"
                  onClick={showList}
                >
                  Cancel
                </Button>
              )}
            </Grid>
            <Grid item xs={6}>
              <Button
                type="submit"
                sx={{ padding: "4%", marginLeft: "6%" }}
                fullWidth
                variant="contained"
                color="secondary"
                // onClick={addEmployee}
              >
                {props.method === "post" ? "Register" : "Update"}
              </Button>
            </Grid>
          </Grid>
        </form>
      </Paper>
    </Grid>
  );
};
export default Eform;
