import * as Yup from "yup";

export const EFormSchema = Yup.object({
  name: Yup.string().min(2).max(25).required("Please enter name"),
  email: Yup.string().email().required("Please enter email"),
  phone: Yup.string()
    .required("Phone number is required")
    .matches(
      /^(\d{3}[-. ]?\d{3}[-. ]?\d{4}|\d{10})$/,
      "Invalid Phonenumber format"
    ),
  address: Yup.string(),
  designation: Yup.string().min(2).required("Designation is required"),
  location: Yup.string().min(3).max(40).required("Location is required"),
  salary: Yup.number("salary must be a number"),
  date_of_joining: Yup.date().required("Please fill"),
});
