import React, { useEffect, useState } from "react";
import "../Styles/Eform.css";
import { Box, Card, createTheme, ThemeProvider } from "@mui/material";

import { Button, Grid, Paper, TextField, Typography } from "@mui/material";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import { useFormik } from "formik";
import signUpSchema from "../Schema/signupvalidation";

const initialValues = {
  name: "",
  email: "",
  phone: "",
  username: "",
  password: "",
};
const Signup = () => {
  const { values, errors, handleBlur, touched, handleChange, handleSubmit } =
    useFormik({
      initialValues: initialValues,
      validationSchema: signUpSchema,
      onSubmit: (values) => {
        console.log(values);
        // e.preventDefault();
        axios.post("/api/signup", values).then((response) => {
          if (response.data.message === "Registered successfully") {
            alert(response.data.message);
            navigate("/");
          } else {
            alert("Unable to register");
          }
        });
      },
    });

  const navigate = useNavigate();

  const theme = createTheme({
    typography: {
      fontFamily: ["Roboto Slab", "serif"].join(","),
    },
  });
  return (
    <div style={{ paddingTop: "20px" }}>
      <ThemeProvider theme={theme}>
        <Card
          sx={{ minWidth: 275, fontFamily: "Roboto Slab, serif" }}
          className="Eform"
        >
          <Paper elevation={24} className="paperStyle">
            <Grid align="center">
              <Grid>
                <Typography
                  className="Header"
                  variant="h4"
                  gutterBottom
                  // sx={{ fontWeight: "bold" }}
                >
                  Signup
                </Typography>
                <Typography
                  variant="caption"
                  sx={{ color: "#FC4A9D", fontWeight: "bold" }}
                >
                  Please fill the form to register
                </Typography>
              </Grid>
            </Grid>
            <Grid>
              <form className="Form" onSubmit={handleSubmit}>
                <TextField
                  fullWidth
                  sx={{ m: 2 }}
                  label="Name"
                  id="name"
                  name="name"
                  variant="outlined"
                  value={values.name}
                  onChange={handleChange}
                  onBlur={handleBlur}
                />
                <Box pl={3}>
                  {errors.name && touched.name ? (
                    <Typography variant="body2" color="error" gutterBottom>
                      {errors.name}
                    </Typography>
                  ) : null}
                </Box>
                <TextField
                  fullWidth
                  sx={{ m: 2 }}
                  label="Email"
                  name="email"
                  id="email"
                  type="email"
                  value={values.email}
                  variant="outlined"
                  onChange={handleChange}
                  onBlur={handleBlur}
                />
                <Box pl={3}>
                  {errors.email && touched.email ? (
                    <Typography variant="body2" color="error" gutterBottom>
                      {errors.email}
                    </Typography>
                  ) : null}
                </Box>
                <TextField
                  fullWidth
                  sx={{ m: 2 }}
                  label="Phone Number"
                  name="phone"
                  id="phone"
                  type="tel"
                  value={values.phone}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  variant="outlined"
                />
                <Box pl={3}>
                  {errors.phone && touched.phone ? (
                    <Typography variant="body2" color="error" gutterBottom>
                      {errors.phone}
                    </Typography>
                  ) : null}
                </Box>
                <TextField
                  fullWidth
                  sx={{ m: 2 }}
                  label="Username"
                  name="username"
                  id="username"
                  value={values.username}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  variant="outlined"
                />
                <Box pl={3}>
                  {errors.username && touched.username ? (
                    <Typography variant="body2" color="error" gutterBottom>
                      {errors.username}
                    </Typography>
                  ) : null}
                </Box>
                <TextField
                  fullWidth
                  sx={{ m: 2 }}
                  name="password"
                  id="password"
                  type="password"
                  label="Password"
                  value={values.password}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  variant="outlined"
                />
                <Box pl={3}>
                  {errors.password && touched.password ? (
                    <Typography variant="body2" color="error" gutterBottom>
                      {errors.password}
                    </Typography>
                  ) : null}
                </Box>
                <Button
                  sx={{ padding: "3%", marginLeft: "3.5%", fontSize: "1.2rem" }}
                  className="login"
                  fullWidth
                  type="submit"
                  variant="contained"
                  color="error"
                  // onClick={submitHandler}
                >
                  Login
                </Button>
              </form>
              <Grid sx={{ marginTop: "20px" }} align="center">
                <Link to="/" className="createlink">
                  Already have an account? Click here
                </Link>
              </Grid>
            </Grid>
          </Paper>
        </Card>
      </ThemeProvider>
    </div>
  );
};

export default Signup;
