import axios from "axios";
import { useEffect, useState } from "react";
import * as React from "react";
import {
  Button,
  CircularProgress,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TablePagination,
  TableRow,
} from "@mui/material";
import Eform from "./Eform";
import CustomizedDialogs from "./Dialogue";

const EmployeeList = () => {
  let [data, setData] = useState([]);
  let [loading, setLoading] = useState(true);
  const [update, setUpdate] = useState(false);
  const [singleValue, setSingleValue] = useState([]);

  // collecting token related info from the session

  const [userToken, setUserToken] = useState(
    sessionStorage.getItem("userToken")
  );
  const [userId, setUserId] = useState(sessionStorage.getItem("userId"));
  const [userRole, setUserRole] = useState(sessionStorage.getItem("role"));

  const config = {
    headers: {
      authorization: "Bearer " + userToken,
    },
  };
  // loading employee list from the db
  useEffect(() => {
    axios
      .get("/api/employeelist", config)
      .then((response) => {
        setData(response.data);
        setLoading(false);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  // deleteing the specific employee doc
  const deleteHandler = (id) => {
    axios
      .delete(`http://localhost:8000/api/employeelist/${id}`, config)
      .then((response) => {
        if (response.data.message === "Employee deleted successfully") {
          alert(response.data.message);
          window.location.reload();
        } else {
          alert(response.data.message);
        }
      })
      .catch((err) => {
        console.log(err.message);
      });
  };

  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(10);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };

  // date formatting function
  const dateFormat = (date) => {
    const newDate = new Date(date);
    const month = newDate.getMonth();
    const day = newDate.getDate();
    const monthString = month >= 10 ? month : `0${month}`;
    const dayString = day >= 10 ? day : `0${day}`;

    return `${newDate.getFullYear()}-${monthString}-${dayString}`;
  };

  // Employee list table
  let finalJSX = (
    <div style={{ paddingTop: "100px" }}>
      {loading ? (
        <div style={{ margin: "10% 45%" }}>
          <CircularProgress />
          <h1>Loading</h1>
        </div>
      ) : (
        <Paper
          sx={{
            width: "98.5%",
            overflow: "hidden",
            marginLeft: "10px",
            marginRight: "10px",
          }}
        >
          <h2 className="App fw-bold py-4 fst-italic">Employee List</h2>
          <TableContainer sx={{ maxHeight: 440 }}>
            <Table stickyHeader aria-label="sticky table">
              <TableHead>
                <TableRow
                  sx={{
                    "& th": {
                      color: "white",
                      backgroundColor: "#47301F",
                      fontSize: "1.25rem",
                      fontWeight: "bold",
                    },
                  }}
                >
                  <TableCell style={{ minWidth: 170 }}>Name</TableCell>
                  <TableCell style={{ minWidth: 170 }}>Email</TableCell>
                  <TableCell style={{ minWidth: 170 }}>Phone</TableCell>
                  <TableCell style={{ minWidth: 170 }}>Designation</TableCell>
                  <TableCell style={{ minWidth: 170 }}>Location</TableCell>
                  <TableCell style={{ minWidth: 170 }}>
                    Date of joining
                  </TableCell>
                  {userRole === "admin" && (
                    <TableCell style={{ minWidth: 170 }}>
                      Update/Delete
                    </TableCell>
                  )}
                </TableRow>
              </TableHead>
              <TableBody sx={{ backgroundColor: "#F1DABF" }}>
                {data
                  .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                  .map((row, i) => {
                    return (
                      <TableRow hover role="checkbox" tabIndex={-1} key={i}>
                        <TableCell
                          sx={{
                            fontSize: "1.05rem",
                            fontWeight: "bold",
                            color: "#47301F",
                          }}
                        >
                          {row.name}
                        </TableCell>
                        <TableCell
                          sx={{
                            fontSize: "1.05rem",
                            fontWeight: "bold",
                            color: "#47301F",
                          }}
                        >
                          {row.email}
                        </TableCell>
                        <TableCell
                          sx={{
                            fontSize: "1.05rem",
                            fontWeight: "bold",
                            color: "#47301F",
                          }}
                        >
                          {row.phone}
                        </TableCell>
                        <TableCell
                          sx={{
                            fontSize: "1.05rem",
                            fontWeight: "bold",
                            color: "#47301F",
                          }}
                        >
                          {row.designation}
                        </TableCell>
                        <TableCell
                          sx={{
                            fontSize: "1.05rem",
                            fontWeight: "bold",
                            color: "#47301F",
                          }}
                        >
                          {row.location}
                        </TableCell>
                        <TableCell
                          sx={{
                            fontSize: "1.05rem",
                            fontWeight: "bold",
                            color: "#47301F",
                          }}
                        >
                          {
                            (row.date_of_joining = dateFormat(
                              row.date_of_joining
                            ))
                          }
                        </TableCell>
                        {userRole === "admin" && (
                          <TableCell
                            sx={{
                              fontSize: "1.05rem",
                              fontWeight: "bold",
                              color: "#47301F",
                            }}
                          >
                            <Button
                              variant="contained"
                              color="success"
                              onClick={() => {
                                updateEmployee(row);
                              }}
                            >
                              Update
                            </Button>
                            &nbsp;
                            <Button
                              variant="contained"
                              color="error"
                              onClick={() => {
                                deleteHandler(row._id);
                              }}
                            >
                              Delete
                            </Button>
                          </TableCell>
                        )}
                      </TableRow>
                    );
                  })}
              </TableBody>
            </Table>
          </TableContainer>
          <TablePagination
            sx={{ backgroundColor: "#E2B179" }}
            rowsPerPageOptions={[10, 25, 100]}
            component="div"
            count={data.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </Paper>
      )}
    </div>
  );

  // for displaying updation form
  const updateEmployee = (val) => {
    if (userRole !== "admin") {
      alert("Only admin can delete an employee");
      window.location.reload();
      return;
    }
    setUpdate(true);
    setSingleValue(val);
  };
  if (update)
    finalJSX = (
      <CustomizedDialogs>
        <Eform method="put" data={singleValue} />
      </CustomizedDialogs>
    );
  return finalJSX;
};

export default EmployeeList;
