import React, { useEffect, useState } from "react";
import "../Styles/Eform.css";
import {
  Box,
  Card,
  createTheme,
  InputAdornment,
  ThemeProvider,
} from "@mui/material";
import PersonIcon from "@mui/icons-material/Person";
import LockIcon from "@mui/icons-material/Lock";

import { Button, Grid, Paper, TextField, Typography } from "@mui/material";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";

const Login = () => {
  const initialValues = { username: "", password: "" };
  const [user, setUser] = useState(initialValues);
  const [formErrors, setFormErrors] = useState({});
  const [isSubmit, setIsSubmit] = useState(false);
  const [errorMessage, setErrorMessage] = useState({
    username: false,
    password: false,
  });
  const navigate = useNavigate();

  const inputHandler = (e) => {
    setUser({ ...user, [e.target.name]: e.target.value });
    // console.log(user);
  };

  const validate = (values, e) => {
    let errors = {};
    const usernameRegex = /^(?=.*\d)[a-zA-Z\d]{3,10}$/;
    const passwordRegex =
      /^(?=.*[A-Z])(?=.*[a-z])(?=.*\d)(?=.*[!@#$%^&*()\-_=+{};:,<.>]).{7,32}$/;
    if (e.target.name === "username") {
      if (!values.username) {
        errors.username = "Username is required!";
      } else if (values.username.length < 3) {
        errors.username = "Username must be greater than 3";
      } else if (values.username.length > 10) {
        errors.username = "Username must be less than 10";
      } else if (!usernameRegex.test(values.username)) {
        errors.username =
          "Username contain atleast one number and rest of them alphabets";
      }
    }
    if (e.target.name === "password") {
      if (!values.password) {
        errors.password = "Password is required!";
      } else if (values.password.length < 7) {
        errors.password = "Password length must be greater than 7";
      } else if (values.password.length > 32) {
        errors.password = "Password length must be less than 32";
      } else if (!passwordRegex.test(values.password)) {
        errors.password =
          "Password must be contain at least one uppercase letter, at least one lowercase letter, at least one number, and at least one special character";
      }
    }
    return errors;
  };

  const handleValidation = (e) => {
    setErrorMessage({ ...errorMessage, [e.target.name]: true });
    setFormErrors(validate(user, e));
  };

  const errorMessageHandler = (e) => {
    setErrorMessage({ ...errorMessage, [e.target.name]: false });
  };
  let username = false,
    pwd = false;
  const formCheck = (data) => {
    let errors = {};

    console.log(errorMessage);

    if (!data.password) {
      console.log("hello");
      setErrorMessage({ ...errorMessage, password: true });
      username = true;
      errors.password = "Password is required!";
    }
    if (!data.username) {
      console.log("hi");
      setErrorMessage({ ...errorMessage, username: true });
      pwd = true;
      errors.username = "Username is required!";
    }
    if (username && pwd)
      setErrorMessage({ ...errorMessage, username: true, password: true });
    return errors;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setFormErrors(formCheck(user));
    setIsSubmit(true);
    // handleLogin();
  };

  useEffect(() => {
    console.log(formErrors);
    console.log(errorMessage);
    if (
      Object.keys(formErrors).length === 0 &&
      isSubmit &&
      user.username !== "" &&
      user.password !== ""
    ) {
      console.log("Login successfully");
      axios.post("/api/login", user).then((response) => {
        if (response.data.message === "Login Successfully") {
          const token = response.data.token;
          const userid = response.data.data._id;
          const role = response.data.data.role;
          console.log(token);
          console.log(userid);
          console.log(role);
          sessionStorage.setItem("userToken", token);
          sessionStorage.setItem("userId", userid);
          sessionStorage.setItem("role", role);
          alert(response.data.message);
          navigate("/list");
        } else {
          alert("Invalid credentials");
          window.location.reload();
        }
      });
    }

    // navigate("/employee");
  }, [formErrors]);

  const theme = createTheme({
    typography: {
      fontFamily: ["Roboto Slab", "serif"].join(","),
    },
  });
  return (
    <div style={{ paddingTop: "80px" }}>
      <ThemeProvider theme={theme}>
        <Card
          sx={{ minWidth: 275, fontFamily: "Roboto Slab, serif" }}
          className="Eform"
        >
          <Paper elevation={24} className="paperStyle">
            <Grid align="center">
              <Grid>
                <Typography
                  className="Header"
                  variant="h4"
                  gutterBottom
                  // sx={{ fontWeight: "bold" }}
                >
                  Login
                </Typography>
                <Typography
                  variant="caption"
                  sx={{ color: "#FC4A9D", fontWeight: "bold" }}
                >
                  Please Login to see employees
                </Typography>
              </Grid>
            </Grid>
            <Grid>
              <form className="Form">
                <TextField
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <PersonIcon />
                      </InputAdornment>
                    ),
                  }}
                  fullWidth
                  sx={{ m: 2 }}
                  label="Username"
                  value={user.username}
                  name="username"
                  variant="outlined"
                  onChange={inputHandler}
                  onBlur={handleValidation}
                  onFocus={errorMessageHandler}
                  error={formErrors.username}
                />
                <br />
                <Box pl={3}>
                  <Typography variant="body2" color="error" gutterBottom>
                    {errorMessage.username && formErrors.username}
                  </Typography>
                </Box>
                <TextField
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <LockIcon />
                      </InputAdornment>
                    ),
                  }}
                  fullWidth
                  sx={{ m: 2 }}
                  name="password"
                  value={user.password}
                  type="password"
                  label="Password"
                  variant="outlined"
                  onChange={inputHandler}
                  onBlur={handleValidation}
                  onFocus={errorMessageHandler}
                  error={formErrors.password}
                />
                <br />
                <Box pl={3}>
                  <Typography variant="body2" color="error" gutterBottom>
                    {errorMessage.password && formErrors.password}
                  </Typography>
                </Box>

                <Button
                  sx={{ padding: "3%", marginLeft: "3.5%" }}
                  className="login"
                  fullWidth
                  type="submit"
                  variant="contained"
                  color="success"
                  onClick={handleSubmit}
                >
                  Login
                </Button>
              </form>
              <Grid sx={{ marginTop: "20px" }} align="center">
                <Link to="/signup" className="createlink">
                  Do not have an account? Click here
                </Link>
              </Grid>
            </Grid>
          </Paper>
        </Card>
      </ThemeProvider>
    </div>
  );
};

export default Login;
