import * as Yup from "yup";

const signUpSchema = Yup.object({
  name: Yup.string().min(2).max(25).required("Please enter your name"),
  email: Yup.string().email().required("Please enter your name"),
  phone: Yup.string()
    .required("Phone number is required")
    .matches(
      /^(\d{3}[-. ]?\d{3}[-. ]?\d{4}|\d{10})$/,
      "Invalid Phonenumber format"
    ),
  username: Yup.string()
    .min(3, "username is too short")
    .max(10, "username is too long")
    .required("Username is required")
    .matches(
      /^(?=.*\d)[a-zA-Z\d]{3,10}$/,
      "Username should contain atlest one number and 2<length<11"
    ),
  password: Yup.string()
    .min(7, "password is too short")
    .max(32, "password is too long")
    .required("Password is required")
    .matches(
      /^(?=.*[A-Z])(?=.*[a-z])(?=.*\d)(?=.*[!@#$%^&*()\-_=+{};:,<.>]).{7,32}$/,
      "Password must contain  \n 1.Atleast one uppercase letter, \n 2.Atleast one lowercase letter,\n 3 Atleast one number, 4.Atleast one special character"
    ),
});

export default signUpSchema;
